.Load p{
    color: rgba(0, 0, 0, 0.589);
    text-align: center;
    font-weight: bold;
    font-size: 26px;
 }
 .Load .Loader,
 .Load .Loader:after {
  border-radius: 50%;
  width: 10em;
  height: 10em;
}
.Load .Loader {
  margin: 60px auto;
  font-size: 10px;
  position: relative;
  text-indent: -9999em;
  border-top: 1.1em solid rgb(109, 109, 109);
  border-right: 1.1em solid rgb(109, 109, 109);
  border-bottom: 1.1em solid rgb(109, 109, 109);
  border-left: 1.1em solid #eeb416;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation: load8 1.1s infinite linear;
  animation: load8 1.1s infinite linear;
}
@-webkit-keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}


